.order-dash-container{
    display: flex;
    margin-top: 10px;
  }
  .dash-order {
    flex: 4 1;
    padding: 20px;
  }
  
  .orderTitleContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .orderAddButton {
    width: 80px;
    border: none;
    padding: 5px;
    background-color: teal;
    color: white;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .orderTop {
    display: flex;
  }
  
  .orderTopLeft,
  .orderTopRight {
    flex: 1 1;
  }
  
  .orderTopRight {
    padding: 20px;
    margin: 20px;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  }
  
  .orderInfoImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
  }
  
  .orderInfoTop {
    display: flex;
    align-items: center;
  }
  
  .orderName {
    font-weight: 600;
  }
  
  .loader-box{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .orderInfoBottom {
    margin-top: 10px;
  }
  
  .orderInfoItem {
    padding: 0 10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  .orderInfoValue {
    font-weight: 300;
  }
  
  .orderBottom {
    padding: 20px;
    margin: 20px;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  }
  
  .orderForm {
    display: flex;
    justify-content: space-between;
  }
  
  .orderFormLeft {
    display: flex;
    flex-direction: column;
  }
  
  .orderFormLeft > label {
    margin-bottom: 10px;
    color: gray;
  }
  
  .orderFormLeft > input {
    margin-bottom: 10px;
    border: none;
    padding: 5px;
    border-bottom: 1px solid gray;
  }
  
  .orderFormLeft >select{
    margin-bottom: 10px;
  }
  
  .orderUploadImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 20px;
  }
  
  .orderFormRight{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  
  .orderUpload{
    display: flex;
    align-items: center;
  }
  
  .orderButton{
    border: none;
    padding: 5px;
    border-radius: 5px;
    background-color: darkblue;
    color:white;
    font-weight: 600;
    cursor: pointer;
  }

  .status{
    padding: 5px 20px;
    width: 30px;
    border-radius: 20px;
    text-align: center;
    font-size: 13px;
    font-weight: 400;
    color: #fff;
  }
  .stat-del{
    background: rgb(166, 231, 166);
  }

  .stat-pending{
    background: rgb(158, 52, 52);
  }

  .btn-delivered{
    padding: 10px;
    border: none;
    color: #fff;
    background: #C09621;
    font-weight: 400;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 20px;
  }

  .ordeItems{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    margin: 10px auto;
}
.ordeItems img{
    width: 50px;
    max-width: 50px;
    height: 50px;
    border-radius: 100px;
}
.sidebar {
  flex: 1 1;
  /* width: 25%; */
  height: calc(90vh - 50px);
  /* height: auto; */
  background-color: rgb(251, 251, 255);
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
  padding-bottom: 20px;
}

.sidebarWrapper {
  padding: 20px;
  color: #555;
}

.sidebarMenu {
  margin-bottom: 10px;
}

.sidebarTitle {
  font-size: 13px;
  color: rgb(187, 186, 186);
}

.sidebarList {
  list-style: none;
  padding: 5px;
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgb(240, 240, 255);
}

.sidebarIcon{
    margin-right: 5px;
    font-size: 20px !important;
}

